'use client';

import { useState, useRef } from 'react';

export default function AffirmationPlayer() {
  const [text, setText] = useState(''); // User input
  const [loading, setLoading] = useState(false); // Button and UX state
  const [error, setError] = useState(''); // API or validation errors
  const audioRef = useRef(null); // Ref for audio player

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Basic validation: input must not be empty or whitespace
    if (!text.trim()) {
      setError('Please enter an affirmation.');
      return;
    }

    setLoading(true);
    setError('');

    try {
      const response = await fetch('/api/text-to-speech', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text }), // Send text in request body
      });

      if (!response.ok) {
        // Try to parse JSON error if available
        let errData;
        try {
          errData = await response.json();
        } catch {
          errData = { message: 'Unexpected server error' };
        }
        throw new Error(errData?.error?.message || errData.message || 'Failed to generate speech.');
      }

      // Get audio blob from server
      const audioBlob = await response.blob();
      const audioUrl = URL.createObjectURL(audioBlob);

      // Set audio source and play
      if (audioRef.current) {
        audioRef.current.src = audioUrl;
        await audioRef.current.play();
      }

      setText(''); // Clear textbox after playing
    } catch (err) {
      console.error('Speech API Error:', err);
      setError(err.message || 'Something went wrong while generating speech.');
    } finally {
      setLoading(false); // Reset loading state
    }
  };

  return (
    <main className="min-h-screen flex flex-col items-center justify-center p-4 bg-gray-50">
      <h1 className="text-3xl font-bold mb-6">Affirmation to Voice</h1>

      <form onSubmit={handleSubmit} className="w-full max-w-md space-y-4">
        {/* Text area for affirmation input */}
        <textarea
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Type your affirmation..."
          className="w-full p-3 border border-gray-300 rounded shadow resize-none"
          rows={4}
        />

        {/* Submit button with loading state */}
        <button
          type="submit"
          disabled={loading}
          className={`w-full py-2 rounded text-white font-semibold transition ${
            loading ? 'bg-gray-400 cursor-not-allowed' : 'bg-blue-600 hover:bg-blue-700'
          }`}
        >
          {loading ? 'Generating...' : 'Speak'}
        </button>

        {/* Error message display */}
        {error && <p className="text-sm text-red-600">{error}</p>}
      </form>

      {/* Audio player for playback */}
      <audio ref={audioRef} className="mt-6 w-full max-w-md" controls />
    </main>
  );
}
